/* --- GOOGLE FONTS --- */
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap');

/* --- CSS VARIABLES (PALETA DE COLORES) --- */
:root {
  --primary-dark-blue: #0a2540;
  --primary-blue: #3a7bd5;
  --accent-cyan: #00d2ff;
  --light-bg: #f4f7f9;
  --white: #ffffff;
  --dark-text: #2c3e50;
  --light-text: #7f8c8d;
  --border-color: #e0e6eb;
  --shadow-color: rgba(10, 37, 64, 0.1);
  --gradient: linear-gradient(45deg, var(--primary-blue), var(--accent-cyan));
}

/* --- ESTILOS GLOBALES --- */
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Poppins', sans-serif;
  background-color: var(--white);
  color: var(--dark-text);
  line-height: 1.6;
}

.home-container {
  overflow-x: hidden;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: 600;
  color: var(--primary-dark-blue);
}

.section-title {
  font-size: 2.5rem;
  text-align: center;
  margin-bottom: 20px;
}

.section-subtitle {
  font-size: 1.1rem;
  text-align: center;
  color: var(--light-text);
  max-width: 700px;
  margin: 0 auto 50px auto;
}

.gradient-text {
  background: var(--gradient);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  text-fill-color: transparent;
}

/* --- HERO SECTION --- */
.hero-section {
  position: relative;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: var(--white);
  padding: 0 20px;
  background: var(--primary-dark-blue); /* Fallback */
}

.hero-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(10, 37, 64, 0.6);
  z-index: 1;
}

.hero-content {
  position: relative;
  z-index: 2;
  max-width: 800px;
}

.logo-container .main-logo {
  width: 80%;
  margin-bottom: 20px;
  filter: brightness(0) invert(1);
}

.main-title {
  font-size: 1.5rem;
  font-weight: 700;
  line-height: 1.2;
  color: var(--white);
}

.hero-subtitle {
  font-size: 1.2rem;
  margin: 20px 0 30px;
  opacity: 0.9;
}

.hero-buttons {
  display: flex;
  justify-content: center;
  gap: 15px;
}

.primary-button, .secondary-button {
  padding: 12px 28px;
  border-radius: 50px;
  border: none;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  gap: 10px;
}

.primary-button {
  background: var(--gradient);
  color: var(--white);
}

.primary-button:hover {
  transform: translateY(-3px);
  box-shadow: 0 10px 20px rgba(0, 210, 255, 0.2);
}

.secondary-button {
  background: transparent;
  color: var(--white);
  border: 2px solid var(--white);
}

.secondary-button:hover {
  background: var(--white);
  color: var(--primary-dark-blue);
  transform: translateY(-3px);
}

.hero-scroll-indicator {
  position: absolute;
  bottom: 30px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 3;
}

.scroll-down {
  width: 24px;
  height: 40px;
  border: 2px solid var(--white);
  border-radius: 12px;
  position: relative;
}

.scroll-down::before {
  content: '';
  position: absolute;
  top: 8px;
  left: 50%;
  transform: translateX(-50%);
  width: 4px;
  height: 8px;
  background: var(--white);
  border-radius: 2px;
  animation: scroll-anim 2s infinite;
}

@keyframes scroll-anim {
  0% { top: 8px; opacity: 1; }
  100% { top: 20px; opacity: 0; }
}

/* --- SECCIONES GENERALES --- */
section {
  padding: 80px 0;
}

section:nth-of-type(odd) {
  background-color: var(--light-bg);
}

/* --- STATS SECTION --- */
.stats-section {
    background: var(--light-bg);
}
.stats-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 30px;
  text-align: center;
}

.stat-card .stat-icon {
  font-size: 2.5rem;
  color: var(--primary-blue);
  margin-bottom: 10px;
}

.stat-card h3 {
  font-size: 2.2rem;
  color: var(--primary-dark-blue);
}

.stat-card p {
  color: var(--light-text);
  font-size: 1rem;
}

/* --- ABOUT SECTION --- */
.about-section {
  position: relative;
  color: var(--white);
  padding: 120px 0;
  overflow: hidden;
}

.video-background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
}
.video-background video {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.video-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(10, 37, 64, 0.85);
}

.about-content h2, .about-content h3 {
  color: var(--white);
}
.about-description {
  max-width: 800px;
  margin: 0 auto 40px auto;
  text-align: center;
  font-size: 1.1rem;
  opacity: 0.9;
}

.about-features {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 30px;
  text-align: center;
}

.feature-item .feature-icon {
  font-size: 2.5rem;
  color: var(--accent-cyan);
  margin-bottom: 15px;
}
.feature-item p {
  opacity: 0.8;
}

/* --- HOW IT WORKS SECTION --- */
.steps-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 40px;
}

.step-card {
  background: var(--white);
  padding: 30px;
  border-radius: 10px;
  box-shadow: 0 5px 25px var(--shadow-color);
  text-align: center;
  position: relative;
  border-top: 4px solid var(--primary-blue);
}

.step-number {
  position: absolute;
  top: -25px;
  left: 50%;
  transform: translateX(-50%);
  width: 50px;
  height: 50px;
  background: var(--gradient);
  color: var(--white);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.5rem;
  font-weight: 700;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

.step-content {
  margin-top: 30px;
}

.step-icon {
  font-size: 2.5rem;
  color: var(--primary-blue);
  margin-bottom: 15px;
}
.step-content p {
  color: var(--light-text);
  font-size: 0.95rem;
}

/* --- FEATURES SECTION --- */
.features-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 30px;
}

.feature-card {
  background: var(--white);
  padding: 30px;
  border-radius: 10px;
  text-align: center;
  transition: all 0.3s ease;
  box-shadow: 0 5px 15px var(--shadow-color);
}

.feature-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 15px 30px var(--shadow-color);
}

.feature-icon-circle {
  width: 70px;
  height: 70px;
  margin: 0 auto 20px auto;
  border-radius: 50%;
  background: linear-gradient(45deg, rgba(58, 123, 213, 0.1), rgba(0, 210, 255, 0.1));
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.8rem;
  color: var(--primary-blue);
}
.feature-card h3 {
  margin-bottom: 10px;
}
.feature-card p {
  color: var(--light-text);
}

/* --- PRICING SECTION --- */
.pricing-cards {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 30px;
  flex-wrap: wrap;
}

.pricing-card {
  background: var(--white);
  border-radius: 15px;
  padding: 40px 30px;
  box-shadow: 0 10px 30px var(--shadow-color);
  flex: 1;
  max-width: 360px;
  min-width: 300px;
  text-align: center;
  transition: all 0.3s ease;
  border: 1px solid var(--border-color);
}

.pricing-card.featured {
  transform: scale(1.05);
  border: 2px solid var(--primary-blue);
  position: relative;
}

.featured-badge {
  position: absolute;
  top: -15px;
  left: 50%;
  transform: translateX(-50%);
  background: var(--gradient);
  color: var(--white);
  padding: 5px 15px;
  border-radius: 20px;
  font-size: 0.8rem;
  font-weight: 600;
}
.pricing-card .card-header h3 {
  font-size: 1.5rem;
}
.pricing-card .price {
  margin: 15px 0;
}
.price .amount {
  font-size: 3rem;
  font-weight: 700;
  color: var(--primary-dark-blue);
}
.price .period {
  color: var(--light-text);
}
.card-header .description {
  color: var(--light-text);
  margin-bottom: 30px;
}

.features-list {
  list-style: none;
  text-align: left;
  margin-bottom: 40px;
}
.features-list li {
  margin-bottom: 15px;
  display: flex;
  align-items: center;
  gap: 10px;
}
.features-list li .fa-check {
  color: var(--primary-blue);
}

.card-button {
  width: 100%;
  padding: 12px;
  border-radius: 8px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  border: 2px solid var(--primary-blue);
  background: transparent;
  color: var(--primary-blue);
}
.card-button:hover, .pricing-card.featured .card-button.primary {
  background: var(--primary-blue);
  color: var(--white);
}
.pricing-card.featured .card-button.primary {
  background: var(--gradient);
  border: none;
}
.pricing-note {
  text-align: center;
  margin-top: 40px;
  color: var(--light-text);
}
.pricing-note a {
  color: var(--primary-blue);
  font-weight: 600;
  text-decoration: none;
}

/* --- TESTIMONIALS SECTION --- */
.testimonials-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  gap: 30px;
}

.testimonial-card {
  background: var(--white);
  padding: 30px;
  border-radius: 10px;
  box-shadow: 0 5px 15px var(--shadow-color);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.testimonial-content p {
  font-style: italic;
  color: var(--light-text);
  margin-bottom: 20px;
  position: relative;
  padding-left: 20px;
}
.testimonial-content p::before {
  content: 'â€œ';
  position: absolute;
  left: -10px;
  top: -10px;
  font-size: 3rem;
  color: var(--primary-blue);
  opacity: 0.2;
}
.testimonial-author {
  display: flex;
  align-items: center;
  gap: 15px;
}
.author-avatar {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background: var(--light-bg);
  color: var(--primary-blue);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.5rem;
}
.author-info h4 {
  margin: 0;
  font-size: 1rem;
}
.author-info p {
  margin: 0;
  font-size: 0.9rem;
  color: var(--light-text);
}

/* --- CTA SECTION --- */
.cta-section {
  background: var(--gradient);
  color: var(--white);
  padding: 80px 0;
  text-align: center;
}
.cta-title {
  font-size: 2.5rem;
  color: var(--white);
  margin-bottom: 15px;
}
.cta-subtitle {
  max-width: 600px;
  margin: 0 auto 30px auto;
  opacity: 0.9;
}
.cta-buttons {
  display: flex;
  justify-content: center;
  gap: 20px;
  flex-wrap: wrap;
}
.cta-buttons .primary-button.large {
  background: var(--white);
  color: var(--primary-blue);
  padding: 15px 35px;
}
.cta-buttons .primary-button.large:hover {
  background: var(--light-bg);
  box-shadow: none;
}
.cta-buttons .secondary-button.large {
  border-color: var(--white);
  padding: 15px 35px;
}
.cta-buttons .secondary-button.large:hover {
  background: var(--white);
  color: var(--primary-blue);
}

/* --- FAQ SECTION --- */
.faq-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30px;
  max-width: 1000px;
  margin: 0 auto;
}
.faq-item {
  background: var(--white);
  padding: 20px;
  border-left: 3px solid var(--primary-blue);
}
.faq-item h3 {
  font-size: 1.1rem;
  margin-bottom: 10px;
}
.faq-item p {
  color: var(--light-text);
}

/* --- FOOTER --- */
.main-footer {
  background: var(--primary-dark-blue);
  color: rgba(255, 255, 255, 0.7);
  padding: 60px 0 20px 0;
}
.footer-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  gap: 40px;
  margin-bottom: 40px;
}
.footer-logo {
  width: 80px;
  margin-bottom: 15px;
  filter: brightness(0) invert(1);
}
.footer-brand p {
  margin-bottom: 20px;
  font-size: 0.9rem;
}
.social-links a {
  color: var(--white);
  font-size: 1.2rem;
  margin-right: 15px;
  transition: color 0.3s ease;
}
.social-links a:hover {
  color: var(--accent-cyan);
}
.footer-links h4, .footer-contact h4 {
  color: var(--white);
  margin-bottom: 15px;
  font-size: 1.1rem;
}
.footer-links ul {
  list-style: none;
}
.footer-links ul li {
  margin-bottom: 10px;
}
.footer-links ul li a {
  color: rgba(255, 255, 255, 0.7);
  text-decoration: none;
  transition: color 0.3s ease;
}
.footer-links ul li a:hover {
  color: var(--white);
}
.footer-contact p {
  font-size: 0.9rem;
  margin-bottom: 10px;
  display: flex;
  align-items: center;
  gap: 10px;
}
.footer-bottom {
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  padding-top: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 0.9rem;
}
.footer-legal a {
  color: rgba(255, 255, 255, 0.7);
  text-decoration: none;
  margin-left: 15px;
  transition: color 0.3s ease;
}
.footer-legal a:hover {
  color: var(--white);
}


/* --- MEDIA QUERIES (RESPONSIVE DESIGN) --- */

@media (max-width: 992px) {
  .section-title { font-size: 2.2rem; }
  .main-title { font-size: 3rem; }
  .stats-grid { grid-template-columns: repeat(2, 1fr); }
  .about-features { grid-template-columns: 1fr; }
  .features-grid { grid-template-columns: repeat(2, 1fr); }
  .faq-grid { grid-template-columns: 1fr; }
}

@media (max-width: 768px) {
  section { padding: 60px 0; }
  .main-title { font-size: 2.5rem; }
  .hero-subtitle { font-size: 1.1rem; }
  .hero-buttons { flex-direction: column; align-items: center; }
  .primary-button, .secondary-button { width: 80%; justify-content: center; }

  .features-grid { grid-template-columns: 1fr; }
  .steps-grid { grid-template-columns: 1fr; }

  .footer-grid { grid-template-columns: repeat(2, 1fr); }
  .footer-brand, .footer-contact { grid-column: 1 / -1; text-align: center; }
  .social-links { justify-content: center; display: flex; }
  .footer-contact p { justify-content: center; }
  .footer-bottom { flex-direction: column; gap: 10px; }
}

@media (max-width: 576px) {
  .main-title { font-size: 2rem; }
  .section-title { font-size: 1.8rem; }
  .stats-grid { grid-template-columns: 1fr; }
  .pricing-card.featured { transform: scale(1); }
  .cta-buttons .primary-button.large,
  .cta-buttons .secondary-button.large {
    width: 100%;
  }
  .footer-grid { grid-template-columns: 1fr; }
  .footer-grid > div { text-align: center; }
  .footer-links ul { padding-left: 0; }
}