@import url('https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300..800;1,300..800&display=swap');

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Open Sans", sans-serif;
}

.container {
    width: 90%;
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px;
}

h1 {
    color: #616161;
}

h2{
    font-size: 30px;
}

p{
    font-size: 20px;
}

.button-p{
    width: 20%;
    height: 70px;
    background-color: #5271ff;
    color: #fff !important;
    font-weight: 800;
    border: 0;
    border-radius: 8px;
    font-size: 22px;
    transition: ease 0.5s;
}

.button-p:hover{
    scale: 1.05;
    transition: ease 0.5s;
    background-color: #7a92ff;
    color: #e4e4e4 !important;
}